.card-container{
    width: 300px;
    overflow: hidden;
    box-shadow: 0px 0px 15px -5px;
    transition: 0.3s;
    animation: ease-in;
    padding: 10px;
}

.card-container:hover{
    transform: scale(1.1);
    box-shadow: 0px 0px 15px 0px;

}

.image-container img{
    overflow: hidden;
    height: 200px;
    padding: 35px;
}

.card-content{
    margin: 1rem;
    margin-top: 0.3rem;
    
}

h3,p{
    margin: 0;
    padding: 0;
}

.card-title{
    margin-bottom: 0.5rem;
}

.btn {
    display: flex;
    justify-content: center;

}

.btn button{
    padding: 0.5rem;
    background-color: white;
    border: none;
    transition: 0.2s;
    margin-bottom: 0.5rem;

}

.btn button:hover{
    background: rgba(27, 156, 252, 0.1);
    transform: scale(1.2);
}

a{
    text-transform: uppercase;
    color: #189CFC;
    text-decoration: none;
    font-weight: bold;
}